import React from 'react';
import './App.css';
import Header from './components/header/index';
import Products from './components/pages/Products';
import ProductDetails from './components/pages/productDetails';
import {Route,Routes} from 'react-router-dom'
import {useState,useEffect} from 'react';
import Electronics from './components/pages/electronics';
import Outdoor from './components/pages/outdoor';
import Travel from './components/pages/travel';
import Health from './components/pages/health';
import axios from 'axios';
import {Link} from "react-router-dom"
import { Typography,Grid} from '@mui/material';
import Sports from './components/pages/sports.js';


function App() {

  const [post,setPost] = useState([]);
  const [epost,setEpost] = useState([]);
  const [spost,setSpost] = useState([]);
  const [opost,setOpost] = useState([]);
  const [tpost,setTpost] = useState([]);
  const [hpost,setHpost] = useState([]);

  useEffect(()=>{
    axios.get("http://localhost:3000/product/list").then((response) => {
      setPost(response.data.productList);

    })
    axios.get("http://localhost:3000/electronics/list").then((response) => {
      setEpost(response.data.electronicsList);
      
    })
    axios.get("http://localhost:3000/sports/list").then((response) => {
      setSpost(response.data.sportsList);
      
    })
    axios.get("http://localhost:3000/outdoor/list").then((response) => {
      setOpost(response.data.outdoorList);
      
    })
    axios.get("http://localhost:3000/travel/list").then((response) => {
      setTpost(response.data.travelList);
      
    })
    axios.get("http://localhost:3000/health/list").then((response) => {
      setHpost(response.data.healthList);
      
    })
  },[]);
  console.log("received from Server",post);
  


  const [name,setName]=useState([])
  const clickCount=name.length
  const shoppingCart=(item)=>{
  setName((prev)=>{
    return [...prev,item]
  })
}


const [query,setQuery]=useState("");
  const searchQuery=(search)=>{
   
  console.log("Searhqueryyyyy....",search);  
  }
  
  
 

  return (
    <div>
      <Grid container>
        <Header count={clickCount} server={post}  query={query} setQuery={setQuery} searchQuery={searchQuery} eserver={epost} sserver={spost} oserver={opost} tserver={tpost} hserver={hpost}/>
        <Typography sx={{marginLeft:{md:"108px",sm:9,xs:5.5},fontSize:"15px",lineHeight:4}}>
        <Link to="/"> Home</Link>
        </Typography>
      </Grid>

      <Routes>
      <Route path="/" exact element={<Products  server={post} cardSearch={query}  eserver={epost} sserver={spost} oserver={opost} tserver={tpost} hserver={hpost}/>}/>
      <Route  path="/productDetails/:description" element={<ProductDetails 
      cartItems={shoppingCart} server={post} eserver={epost} sserver={spost} oserver={opost} tserver={tpost} hserver={hpost}/>} />
      <Route path="/sports" exact element={<Sports cardSearch={query} sserver={spost}/>}/>
      <Route path="/outdoor" exact element={<Outdoor cardSearch={query}  oserver={opost} />}/>
      <Route path="/travel" exact element={<Travel cardSearch={query}  tserver={tpost}/>}/>
      <Route path="/health" exact element={<Health cardSearch={query}  hserver={hpost}/>}/>
      <Route path="/electronics" exact element={<Electronics cardSearch={query}  eserver={epost}/>}/>
      </Routes>

    </div>
  );

  }

export default App;
