import {Typography,Box,List,Drawer ,Radio, RadioGroup, FormControl,
  FormControlLabel, ListItem, ListItemButton, ListItemIcon,IconButton} from '@mui/material'
import React from 'react';
import { useState} from 'react';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import MenuIcon from '@mui/icons-material/Menu';


const item=["All Categories","Electronics","Outdoor","Travel","Sports","Health",]
const options = ["0 - 1000", "1000 - 2000", "2000+"];

const DrawerComp = ({categoryFilter,filterChanged}) => {


  const [open, setOpen] = useState(false);
  const [change, setChange] = useState(false);



  const categoryHandler=(data)=>{
    console.log("category...",data);
    const FilCat = categoryFilter(data);
  }

  const handleChange = (e) => {
    filterChanged(e.target.defaultValue, true)
    setOpen(!open);
  };

  return(
    <>
      <IconButton sx={{color:"#080867"}} 
      onClick={() => setChange(!change)}>
      <MenuIcon />
      </IconButton>
      <Drawer open={change} onClose={() => setChange(false)} anchor="left">
      
      <Typography sx={{marginLeft:{md:"2px"},fontWeight:"bold",fontSize:{md:"20px",sm:"15px"}}}>Top Results</Typography>
      <Typography sx={{borderBottom:"1px solid black",fontWeight:"bold",fontSize:{md:"20px",sm:"15px"}}}>Filters</Typography>


      <Box  sx={{borderBottom:"1px solid black"}}>

        <Typography sx={{fontWeight:"bold",fontSize:"13px"}}>Price</Typography>
         
        <FormControl sx={{ "&.MuiFormControlLabel-label": { fontSize: 15, }, "& .MuiSvgIcon-root": { fontSize: 15, }, paddingLeft:{md:4,sm:4,xs:4} }}>
          <RadioGroup name="radio-group">
              {options.map((i) => (
          <FormControlLabel value={i} control={<Radio onChange={handleChange} />} label={i} />))}
          </RadioGroup>
        </FormControl>
      </Box>
      <Box>
        <Typography sx={{fontWeight:"bold",fontSize:"13px"}}>Category</Typography>
          <List  sx={{borderBottom:"1px solid black",font:"bold"}} >
          {item.map((data)=>
            <ListItemButton >
              <ListItem >
                  <ListItemIcon sx={{font:"bold",color:"black"}} onClick={()=>{categoryHandler(data)}}>
                    {data}
                  </ListItemIcon>
              </ListItem>
              {item ? <ExpandMore sx={{color:"#3c76b9"}} /> : <ExpandLess sx={{color:"#3c76b8"}} />}
            </ListItemButton>
          )}
          </List>
      </Box>
      </Drawer>
      </>
  )
}

export default DrawerComp;
