import { List ,ListItem,useTheme,useMediaQuery,AppBar, IconButton, 
Toolbar,Box, Typography, TextField, InputAdornment } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Grid from '@mui/material/Grid';
import React from 'react';
import ecom from '../../../src/images/Click.png';
import {Link} from 'react-router-dom';

const links=["Electronics","Sports","Outdoor","Travel","Health"]



const Header = ({count,query,setQuery,searchQuery}) => {

  const theme = useTheme();
  
  const onChangeHandler=(e)=>{
    setQuery(e.target.value)
    searchQuery(query);
  }
  console.log("data from",query);


  return (
    <>
    <AppBar position="sticky" color="default" sx={{boxShadow:'none'}}>
      <Toolbar>
        {
         
          <Grid container   sx={{display:'flex'}}
            alignItems="center"
            justifyContent="center">
          
            <Grid item md={5} xs={6}sx={{display:"flex",marginRight:{md:4}}}> 
            <Link to="/"><Box sx={{cursor:'pointer',display:{xs:"block",sm:"block",md:"block"},width:{sm:180,md:200,xs:100},marginLeft:{sm:3,md:2.5,xs:2},height:{md:45  }}}
              component="img"
              src={ecom} 
              alt="CLickbuy logo"
              href="/home"

              /></Link>
            {/* <Box>
              <Typography  sx={{marginLeft:2,width:{sm:100},fontSize:{md:27,sm:20,xs:12}}}>
                  | Cheers
              </Typography>
            </Box> */}
            </Grid>
          
            <Grid item md={3} xs={3} >
            <TextField onChange={onChangeHandler}  sx={{marginLeft:{sm:10},marginTop:{xs:2, sm:1, md:1}, width:{xs:100,sm:120,md:230}, 
            [`& fieldset`]:{borderRadius:20,borderColor:"#080867",height:{ xs:30, sm:35, md:35},padding:0 }}} size="small" variant="outlined"
            InputProps={{
                endAdornment:(
                <InputAdornment>
                <SearchIcon sx={{cursor:"pointer", paddingBottom:1}}/>
                </InputAdornment>
            )
           
            }}>
  
            </TextField>
            </Grid>
   
            <Grid md={1} sx={{paddingLeft:{md:"50px"},marginLeft:{sm:3},color:"black",display:{xs:"block",sm:"block",md:"block"}}} >
              <IconButton >
                  <AccountCircleIcon sx={{color:"black"}}/>
              </IconButton>
            </Grid>
            <Grid md={1} sx={{marginLeft:{sm:2}}}>
            <IconButton>
                  <ShoppingCartOutlinedIcon sx={{color:"black"}}/>
                  <Typography>{count}</Typography>
            </IconButton>
            </Grid>
          </Grid>
          }
      </Toolbar>
    </AppBar>
  
    <Grid container sx={{ borderTop:1,borderBottom:1,borderColor:'blue',display:{xs:"none",sm:"block"}}} >
      <List   sx={{ display:"flex", width:'500px',paddingLeft:{md:'92px',sm:7 },fontWeight:'600'}}>
       
         <ListItem >
          <Link style={{textDecoration:"none",color:"black"}} to="/electronics">Electronics</Link> 
        </ListItem>
        <ListItem >
          <Link style={{textDecoration:"none",color:"black"}} to="/sports">Sports</Link>
        </ListItem>
        <ListItem >
          <Link style={{textDecoration:"none",color:"black"}} to="/outdoor">Outdoor</Link>
        </ListItem>
        <ListItem >
          <Link style={{textDecoration:"none",color:"black"}} to="/travel">Travel</Link>  
        </ListItem>
        <ListItem >
          <Link style={{textDecoration:"none",color:"black"}} to="/health">Health</Link>  
        </ListItem>
        </List>  
    </Grid>
  
    </>
  )
}
export default Header;
