import React from 'react';
import { Button, Grid} from '@mui/material';
import Box from "@mui/material/Box"
import Typography from '@mui/material/Typography';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import LocalShippingRoundedIcon from '@mui/icons-material/LocalShippingRounded';
import { Link, useParams} from "react-router-dom";
import { useEffect ,useState} from 'react';
import ShowMoreText from "react-show-more-text";

const ProductDetails = ({server,eserver,sserver,oserver,tserver,hserver,cartItems}) => {

  const productsList=server;
  const eList=eserver;
  const sList=sserver;
  const oList=oserver;
  const tList=tserver;
  const hList=hserver;


  console.log("data from productsList",productsList)
  const [singleProduct,setSingleProduct] = useState([]);
  const {description}= useParams();

        //  const { points } = useParams()
  // console.log(name);
      useEffect(() => {
          const findProd=()=>{
          const newProduct = productsList.find((dataOne) => dataOne.description === description)
          const eProduct = eList.find((dataTwo) => dataTwo.description === description)
          const sProduct = sList.find((dataThree) => dataThree.description === description)
          const oProduct = oList.find((dataFour) => dataFour.description === description)
          const tProduct = tList.find((dataFive) => dataFive.description === description)
          const hProduct = hList.find((dataSix) => dataSix.description === description)
          setSingleProduct(eProduct||sProduct||oProduct||tProduct||hProduct||newProduct )
          console.log("new product data......",newProduct)
        }
        findProd();
          })
       
  const [data,setData]=useState(1);
   //Add to cart 
  const[cart,setCart]=useState([]);
  const addToCart=(product)=>{
    // console.log("in ADD to Cart")
    setCart([...cart,product])
    cartItems(product)
  }
  //Calculating total price 
  const totalNum=singleProduct.points*data;
  // console.log("total price of the chosen products",totalNum)
  const [expand, setExpand] = useState(false);
  const onClick = () => {setExpand(!expand); };
 

  return (
   
     <Box sx={{maxWidth:1000 , margin:"auto"}}>
     <Grid container>
      <Grid item md={6} xs={12} sm={6} > 
        <Box component="img" sx={{cursor:"pointer", width:{xs:400,sm:300,md:450},
        marginLeft:{sm:15,xs:5},marginTop:5,marginLeft:{md:3},
        maxHeight:"400px",border:"1px solid black"}} src={singleProduct.img}>
        </Box>
      </Grid>
      
      <Grid item md={6} xs={12} sm={6} sx={{paddingRight:{sm:5}}}> 
      
          <>
        <Box sx={{margin:5,marginBottom:2,borderBottom:"1px solid black"}}>
        <Typography sx={{marginBottom:1}}>
          {singleProduct.description}
        </Typography>
        <Typography sx={{marginBottom:2}}>
          <Link path="/" style={{textDecoration:"none"}}>  {singleProduct.name}</Link>
        </Typography>
        <Box sx={{display:"flex"}}>
        <Typography> Price:</Typography>
        <Typography sx={{paddingBottom:1,fontWeight:"bold"}}>
         {`${singleProduct.points} Points` }
        </Typography>
        </Box>       
        <Typography sx={{display:"flex", paddingBottom:1}}>
        <CheckCircleIcon sx={{backgroundColor:"white",color:"green",borderRadius:"15px",fontSize:"1rem"}}/>
        Item price includes shipping and handling
        </Typography>
        <Box sx={{display:"flex",paddingBottom:2}}>
        <Box sx={{display:"flex",marginRight:"10px",border:"1px solid black",borderRadius:1}}>
          <Button onClick={()=>setData(data-1)} sx={{color:"#212121",backgroundColor:"#e0e0e0",minWidth:{xs:30,sm:30,md:45},borderRight:"1px solid black"}}>-</Button>
          <Box sx={{color:"#212121",minWidth:{xs:30,sm:30,md:45},textAlign:"center",paddingTop:1}}>{data}</Box>
          <Button  onClick={()=>setData(data+1)} sx={{color:"#212121",backgroundColor:"#e0e0e0",minWidth:{xs:30,sm:30,md:45},borderLeft:"1px solid black"}}>+</Button>
        </Box>
        <Button onClick={()=>{addToCart(singleProduct)}}sx={{backgroundColor:"#3c76b9",width:"300px",color:"white",'&:hover':{background: "blue"}}}>
            <ShoppingCartIcon sx={{color:"white",marginRight:2}} carticon={cart.length}/> Add to cart
        </Button>
        </Box>
        <Typography sx={{display:"flex",paddingBottom:2}}>
          <FavoriteBorderOutlinedIcon sx={{color:"#3c76b9"}}/>
          <Link to="/" style={{textDecoration:"none"}}>Add to Wish List
          </Link>
        </Typography>
        </Box>  
        <Box  sx={{borderBottom:"1px solid black",display:"flex",marginLeft:5,marginRight:5}}>
          <Typography sx={{marginBottom:1}}>Total</Typography>
          {/* {singleProduct.points} */}
          <Typography sx={{fontWeight:"bold",marginLeft:"auto"}}>{`${totalNum} Points`}</Typography></Box>
          <Typography sx={{margin:5,display:"flex"}}>
            <LocalShippingRoundedIcon sx={{marginRight:1}}/>
            Usually ships within 7 business days
          </Typography>
        <Box sx={{margin:5,border:"1px solid black",padding:2}}>
          <Typography sx={{fontWeight:"bold"}}>
          Product Description
          </Typography>
          <Typography > {singleProduct.description}</Typography>
            {singleProduct.description}
          <Typography sx={{paddingTop:"23px", paddingBottom:"50px",color:"blue", marginRight:"25px", marginLeft:"10px"}} > 
          {singleProduct.title1}
            <ShowMoreText
            lines={2}
            more={"Show More"}
            less={"Show Less"}
            onClick={onClick}
            expanded={expand}
            width={"100%"}
            
            >
              {singleProduct.description}

            </ShowMoreText>
          </Typography>  
            {/* <Button>
              <Typography>See more </Typography>
              <KeyboardArrowDownIcon/>
            </Button> */}
        </Box>
         </>
          
      </Grid>
      {/* <Grid item md={1} xs={1} sm={1}></Grid> */}
     
     </Grid>
     </Box>
    
  )
}
export default ProductDetails;
