import React, { useState,useEffect } from 'react';
import Grid from '@mui/material/Grid';
import { Typography,Box,List ,Radio,RadioGroup,TextField, FormControl,FormControlLabel, ListItem, ListItemButton, ListItemIcon} from '@mui/material'
import {Link} from "react-router-dom";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined"
import { IconButton } from '@mui/material';
import {MenuItem,Select} from '@mui/material';
import SortData from './sort/index';
import Filter from './leftContainer/Filter';
import DrawerComp from '../header/DrawerComp';


const item=["All Categories(16425)","Electroincs","Outdoor","Travel","Sports","Health",]
const Products = ({cardSearch,server,eserver,sserver,oserver,tserver,hserver}) => {
  const filterItems = cardSearch;
  console.log("filterItemsss..",cardSearch);
  const prodata=server;
  const eList = eserver;
  const sList = sserver;
  const oList = oserver;
  const tList = tserver;
  const hList = hserver;

  console.log("from server......",server)
  const[filtereddata,setFilteredData]=useState(server)

  console.log("filtereddata......",filtereddata);

  //Category Filter logic
  const categoryFilter=(text)=>{
    console.log("text..",text);
    if(text==="All Categories"){
      setFilteredData( prodata)
    }
    else if (text==="Electronics"){
      setFilteredData( eList)
    }
    else if (text==="Outdoor"){
      setFilteredData( oList)
    }
    else if (text==="Sports"){
      setFilteredData( sList)
    }
    else if (text==="Travel"){
      setFilteredData( tList)
    }
    else if (text==="Health"){
      setFilteredData( hList)
    }
  }

//Sorting logic 
  const sortHandler = (val) => {
    console.log("val data...",val);
    if(val === "lowest"){
      const sorted = [...filtereddata].sort ((a,b) =>{
      return a.points>b.points?1:-1
      })
    setFilteredData(sorted);
    }
    

    if(val === "highest"){
      const sorted = [...filtereddata].sort ((a,b) =>{
      return a.points<b.points?1:-1
      })
    setFilteredData(sorted);
    }

    if(val === "All"){
      setFilteredData(server);
    }
  }

  //Price Filter logic
  const filterChanged = (selectedItem, byPrice) => {
    let serverData = Object.values(prodata).flat(2)
    if(byPrice){
      if(selectedItem.includes('-')){
        const splitsValues = selectedItem.split('-')
        const filteredItems =serverData.filter(item=>{
          return Number(item.points) > Number(splitsValues[0].trim()) && Number(item.points) < Number(splitsValues[1].trim())
        })
        setFilteredData(filteredItems)
      }
      else if(selectedItem.includes('+')){
        const splitsValues = selectedItem.split('+')
        const filteredItems = serverData.filter(item=>{
          return Number(item.points) > Number(splitsValues[0].trim())
        })
        setFilteredData(filteredItems)
      }
    }
  }



  return (


   
      <Grid container>
        <Grid item md={1} sm={1} xs={1} >
        </Grid>
        
        <Grid item  sx={{display:{sm:"block",md:"block",xs:"none"}}}>
        <Filter md={2} sm={2} categoryFilter={categoryFilter} filterChanged={filterChanged} />
       
        </Grid>

        <Grid item  sx={{display:{xs:"block",sm:"none",md:"none"}}}>
        <DrawerComp categoryFilter={categoryFilter} filterChanged={filterChanged} />

        </Grid>
       
        <Grid item sx={{position:"relative", marginLeft:{xs:0, sm:1, md:0}}} md={8} sm={8} xs={8}>
        <SortData sortHandler={sortHandler }/>

        <Box sx={{
          maxWidth: 1000, margin: '0 auto', display: "flex", flexWrap: "wrap", flexDirection: "row",
          overflow: "hidden", paddingLeft: 6
        }}>
        {  
        filtereddata.filter(data=>{
          if(cardSearch==="")
          return data;
          else if (data.name.toLowerCase().includes(cardSearch.toLowerCase()))
          return data;
        })
        .map(data => {
              return (
                <Box sx={{
                  height: 300, width: 200, border: "1px solid gray", marginTop: 7, marginRight: 3,
                  marginBottom: 1, padding: 2
                  }} key={data.id}>
                  <IconButton sx={{ float:"right"}}>
                  <FavoriteBorderOutlinedIcon sx={{ color: 'blue',top:0,float:'right'}} />
                  </IconButton>
                  <Box component="img" sx={{ height:160,float:"right"}} src={data.img} />
                  
                  <Typography sx={{ fontWeight: 800, textAlign: "center", textTransform: "uppercase" }}>
                    {data.name}
                  </Typography>
                  <Typography sx={{ minHeight: 70,minWidth:40, textAlign: 'center' }} >
                    {data.description}
                  </Typography>
                  <Link to={`/ProductDetails/${data.description}`} style={{ textDecoration: 'none' }}>
                    <Typography  sx={{ textAlign: 'center', fontWeight: 'bold' }}>{data.points}
                    </Typography>
                  </Link>
                </Box>
              )
            })}
          </Box>
          </Grid >
          <Grid item md={1} sm={1} xs={1} >
          </Grid>

      </Grid>
      
    
  )
}
 
export default Products;

