import {MenuItem,Select,Box} from '@mui/material';
import React, { useState } from 'react';


const SortData = (props) => {
  const [points,setPoints]=useState("All");
 
  const sorting=(data)=>{
    const value = props.sortHandler(data);
  }
  return (
    <Box sx={{position:"absolute",right:{md:145,sm:145,xs:60},top:0}}>
    <Select 
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        value={points}
        onChange={(e)=>setPoints(e.target.value)}
        style={{width:100,height:40,position:'absolute'}}
        >
        <MenuItem  onClick= {() => sorting ("All")}  value={"All"}> <em>All</em> </MenuItem>
        <MenuItem onClick= {() => sorting ("lowest")} value={"lowest"}> lowest</MenuItem>
        <MenuItem onClick= {() => sorting ("highest")}  value={"highest"}>highest</MenuItem>
    </Select>
    </Box>
  )
}

export default SortData;
